import opg from 'ts/opg.ts';
import PopUp from 'ts/ui/Popup';
import {Languages} from 'ts/util/Languages';

const lpg = Languages.package;
console.log('Languages:', Languages);

opg.api({
	getSubject: 'Information/GetInformationSubContentById',
	'addSubject!post': 'Information/AddInformationSubContent',
	'updateSubject!post': 'Information/UpInformationSubContent',
	'upload!UPLOAD': 'upload/files',
});

const informationId = opg.request['id'];
const subjectId = opg.request['subId'];

let imgUrl;

let format = {
	0: lpg.pureText,
	1: lpg.picture,
	2: lpg.link,
	3: lpg.richText,
};
let formatType;


let panel = $('#panel');

let selType = $('#selType').on('change', function () {


	formatType = ~~selType.val();
	switch (formatType) {
		case 0:
			panel.html(`<textarea style="width: 460px; height: 230px;"></textarea>`);
			break;
		case 2:
			panel.html(`<form>
						<input name="text" type="text" placeholder="${lpg.linkText}" style="width: 100%;"/>
						<br>
						<input name="url" type="text" placeholder="${lpg.linkHref}" style="width: 100%;margin-top: 0.5em;"/>
				</form>`);
			break;

		default:
			panel.empty();
	}
});


if (subjectId) {
	opg.api.getSubject({id : subjectId} , (data)=>{
		//debugger;
		$('#lbFormatType').jsonToFields(data[0]);
		selType.trigger('change').prop('disabled' , true);
	});
}


window['doSave'] = function (pop: PopUp, listSubjects: Function) {
	let content;
	switch (formatType) {
		case 0 :
			content = panel.find('textarea').eq(0).val();
			break;
		case 1 :
			content = imgUrl;
			break;
		case 2 :
			let txt, url;
			panel.find('input').each((i, ele) => {
				let val = (ele as HTMLInputElement).value;
				if (!val) {
					return opg.warn(lpg.nullInputWarn);
				}
				if (i === 0)
					txt = val;
				else
					url = val;
			});
			content = `<a href="${url}">${txt}</a>`;
			break;
		default:
			opg.warn(lpg.plsSelect);
			return;
	}

	if (!content) {
		opg.warn(lpg.nullInputWarn);
	}
	else {
		if (subjectId) {
			opg.api.updateSubject({content: content, id: subjectId}, data => {
				listSubjects();
				pop.close();
			});
		}
		else {
			opg.api.addSubject({formatType, informationId, content}, function (data) {
				listSubjects();
				pop.close();
			});
		}
	}

	return true;

};